
import XImg from 'vux/src/components/x-img/index.vue'

import {exchangeLog} from '../config'
import VScroll from '../components/VScroll'
export default {
  name: 'exchange',
  head: {
    title: {
      inner: '兑换记录'
    }
  },
  data () {
    return {
      height: '',
      loading: false,
      form: {
        userId: 0,
        limit: 5,
        pageIndex: 0
      },
      list: [], // 下拉更新数据存放数组
      more: []  // 上拉更多的数据存放数组
    }
  },
  components: {
    XImg,
    VScroll
  },
  mounted () {
    if (this.$route.params.userId === 'null') {
      this.$router.replace('/login')
    } else {
      this.form.userId = this.$route.params.userId
      this.getList(() => {}, null)
    }
  },
  methods: {
    statusNoMore () {
      this.$el.querySelectorAll('.load').forEach(el => {
        el.style.display = 'none'
      })
      this.$el.querySelectorAll('.no-more').forEach(el => {
        el.style.display = 'block'
      })
    },
    statusLoad () {
      this.$el.querySelectorAll('.load').forEach(el => {
        el.style.display = 'block'
      })
      this.$el.querySelectorAll('.no-more').forEach(el => {
        el.style.display = 'none'
      })
    },
    statusInit () {
      this.$el.querySelectorAll('.load').forEach(el => {
        el.style.display = 'none'
      })
      this.$el.querySelectorAll('.no-more').forEach(el => {
        el.style.display = 'none'
      })
    },
    getList (done, status) {
      const This = this
      this.$http({
        method: 'jsonp',
        url: exchangeLog,
        jsonp: 'callback',
        jsonpCallback: 'json',
        params: this.form,
        before: () => {
          if (status) {
            this.list = []
          }
        }
      })
      .then(res => {
        res.body.data.productList.forEach(el => {
          This.list.push(el)
        })
        if (res.body.data.productList.length < This.form.limit) {
          this.statusNoMore()
        } else {
          this.statusLoad()
        }
        done()
      })
    },
    onRefresh (done) {
      this.form.pageIndex = 0
      this.statusInit()
      this.getList(done, 1)
    },
    onInfinite (done) {
      this.form.pageIndex ++
      if (this.list.length % this.form.limit) {
        this.statusNoMore()
      } else {
        this.$el.querySelectorAll('.load').forEach(el => {
          el.style.display = 'block'
        })
        this.getList(done, 0)
      }
    }
  }
}



